// ============================================================================
// FONCTION D'ADAPTATION DU GENRE
// ============================================================================

export function adaptMessageGender(
  message: string,
  speakerGender?: Gender,
  hostGender?: Gender
): string {
  let adapted = message;

  // Adaptation pour orateur f√©minin
  if (speakerGender === 'female') {
    // Fr
    adapted = adapted.replace(/Bonjour Fr√®re/g, 'Bonjour S≈ìur');
    adapted = adapted.replace(/Fr√®re \*{speakerName}\*/g, 'S≈ìur *{speakerName}*');
    adapted = adapted.replace(/notre orateur invit√©/g, 'notre oratrice invit√©e');
    // Pt
    adapted = adapted.replace(/Ol√° Irm√£o/g, 'Ol√° Irm√£');
    adapted = adapted.replace(/Irm√£o \*{speakerName}\*/g, 'Irm√£ *{speakerName}*');
    adapted = adapted.replace(/nosso orador convidado/g, 'nossa oradora convidada');
    // Cv
    adapted = adapted.replace(/Bon dia Mon/g, 'Bon dia Mana');
    adapted = adapted.replace(/Mon \*{speakerName}\*/g, 'Mana *{speakerName}*');
  }

  // Adaptation pour contact d'accueil f√©minin
  if (hostGender === 'female') {
    // Fr
    adapted = adapted.replace(/Fr√®re \*{hostName}\*/g, 'S≈ìur *{hostName}*');
    adapted = adapted.replace(/notre fr√®re/g, 'notre s≈ìur');
    // Pt
    adapted = adapted.replace(/Irm√£o \*{hostName}\*/g, 'Irm√£ *{hostName}*');
    adapted = adapted.replace(/nosso irm√£o/g, 'nossa irm√£');
    // Cv
    adapted = adapted.replace(/Mon \*{hostName}\*/g, 'Mana *{hostName}*');
    adapted = adapted.replace(/nu mon {hostName}/g, 'nu mana {hostName}');
  }

  // Adaptation pour couple
  if (hostGender === 'couple') {
    // Fr
    adapted = adapted.replace(/Fr√®re \*{hostName}\*/g, '*{hostName}*');
    adapted = adapted.replace(/tu vas bien/g, 'vous allez bien');
    // Pt
    adapted = adapted.replace(/Irm√£o \*{hostName}\*/g, '*{hostName}*');
    adapted = adapted.replace(/estejas bem/g, 'estejam bem');
    adapted = adapted.replace(/tua disponibilidade/g, 'vossa disponibilidade');
    adapted = adapted.replace(/teu acolhimento/g, 'vosso acolhimento');
    // Cv
    adapted = adapted.replace(/Mon \*{hostName}\*/g, '*{hostName}*');
    adapted = adapted.replace(/bu sta bon/g, 'nh√≥s sta bon');
  }

  return adapted;
}
